.table-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
table {
    background-color: rgba(255,255,255,.33);
    color: #282c34;
    border-radius: 5px;
    border: 1px solid gray;
    text-align: center;
}
tr {
    margin: 0 auto;
    text-align: center;
}